.App {
  text-align: center;
  padding-top: 30px;
  width: 100%;
  max-width: 700px;
  margin: 0 auto;
}

.SearchForm-wrapper{
    display: flex;
    justify-content: center;
}
.MoviesList{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 16px;
}
.MoviesList-item{
    margin-bottom: 30px;
    width: 48%;
}
.control{
    margin-bottom: 15px;
}
input:focus{
    outline: none;
    box-shadow: inset 0 1px 2px rgba(10, 10, 10, 0.1) !important;
    border-color: #dbdbdb !important;
    border: 1px solid transparent;
}
.detail .button{
    margin-bottom: 15px;
}
.detail h1{
    font-size: 25px;
    font-weight: 700;
    margin-bottom: 15px;
}
.detail img{
    margin-bottom: 15px;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    border-radius: 5px;
    transition:all 0.3s cubic-bezier(.25,.8,.25,1);
}
.detail img:hover {
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}
.detail h3{
    font-weight: 700;
    margin-bottom: 15px;
    padding-bottom: 15px;
    border-bottom: 1px solid #ccc;
}
.detail span{
    display:none;
}
.detail p{
    margin-bottom: 20px;
}
